import AboutServiceInterface from "../../../domain/services/AboutServiceInterface";
import {Axios} from "axios";

export default class AboutService implements AboutServiceInterface {

    constructor(private axiosInstance: Axios) {

    }

    async findOne() {
        return new Promise((resolve, reject) => {
            this.axiosInstance.get('/portfolio/rest-api/about.json')
                .then((result) => {
                    let entity = result.data;

                    this.axiosInstance.get('/portfolio/rest-api/contacts.json')
                        .then((result) => {
                            entity.contacts = result.data;
                            resolve(entity);
                        })
                        .catch((e) => {
                            reject(e);
                        });

                    // resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
