import SkillServiceInterface from "../../../domain/services/SkillServiceInterface";
import {Axios} from "axios";

export default class SkillService implements SkillServiceInterface {
    constructor(private axiosInstance: Axios) {

    }

    async findAll() {
        return new Promise((resolve, reject) => {
            this.axiosInstance.get('/portfolio/rest-api/skills.json')
                .then((result) => {
                    resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
