import React from 'react';
import ImageSliderWidget from "../../../../../../../packages/widgets/imageSlider/ImageSliderWidget";
import Html from "../../../../../../../packages/widgets/html/Html";

export default function ProjectTab(props) {
    let items = props.projects;
    return (
        <>
            {items.map(function (item, key) {

                return (
                    <div key={key} className="card mt-3">
                        <div className="card-body">

                            <div className="float-left pr-3">
                                {item.images ? (
                                    <div
                                        style={{width: '350px', maxWidth: '100%'}}>
                                        <ImageSliderWidget
                                            items={item.images}
                                            showThumbnails={false}
                                            showBullets={true}
                                            showPlayButton={false}
                                        />
                                    </div>
                                ) : (
                                    <img src="/img/no-image.png" alt="" style={{width: "350px"}}/>
                                )}
                            </div>

                            <div>

                                <h5 className="card-title">
                                    {item.title}
                                </h5>

                                {item.site ? (
                                    <p className="card-text">
                                        <i className="fas fa-link"></i>
                                        &nbsp;
                                        <a href={item.site} target="_blank" rel="noreferrer">
                                            {item.site}
                                        </a>
                                    </p>
                                ) : null}

                                <p className="card-text">
                                    <span title="Компания">{item.company}</span> | <span
                                    title="Должность">{item.position}</span>
                                </p>

                                <p className="card-text">
                                    <Html>
                                        {item.description}
                                    </Html>
                                </p>

                                <h6 className="card-subtitle mb-2">
                                    Основной стек
                                </h6>

                                <p className="card-text">
                                    {item.techUnit.map(function (item, key) {
                                        return (
                                            <span key={key} className="badge badge-primary ml-1 mr-1">
                                            {item}
                                        </span>
                                        );
                                    })}
                                </p>
                            </div>

                        </div>
                    </div>
                );
            })}

        </>
    );
};
