import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import AboutService from "../../infrastructure/persist/fs/AboutService";
import axios from "axios";
import UrlHelper from "../../../../../packages/ext/html/helpers/UrlHelper";
import ContactService from "../../infrastructure/persist/fs/ContactService";
import HomeService from "../../infrastructure/persist/fs/HomeService";
import CompanyService from "../../infrastructure/persist/fs/CompanyService";
import ProjectService from "../../infrastructure/persist/fs/ProjectService";
import SkillService from "../../infrastructure/persist/fs/SkillService";

export default function configureDi(domainName: string) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    const axiosInstance = axios.create();
    axiosInstance.defaults.baseURL = UrlHelper.getLocation().origin;

    bundleDiConfigurator.bind("services.about", new AboutService(axiosInstance));
    bundleDiConfigurator.bind("services.contact", new ContactService(axiosInstance));
    bundleDiConfigurator.bind("services.home", new HomeService(axiosInstance));
    bundleDiConfigurator.bind("services.company", new CompanyService(axiosInstance));
    bundleDiConfigurator.bind("services.project", new ProjectService(axiosInstance));
    bundleDiConfigurator.bind("services.skill", new SkillService(axiosInstance));

}
