import HomeServiceInterface from "../../../domain/services/HomeServiceInterface";
import {Axios} from "axios";

export default class HomeService implements HomeServiceInterface {
    constructor(private axiosInstance: Axios) {

    }

    async findOne() {
        return new Promise((resolve, reject) => {
            this.axiosInstance.get('/portfolio/rest-api/home.json')
                .then((result) => {
                    resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
